import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import {Row,Col,Container,Header,Main,Footer,Avatar} from 'element-ui';

// import 'element-ui/lib/theme-chalk/index.css';
import './assets/iconfont.css';

Vue.config.productionTip = false
Vue.use(Row);
Vue.use(Col);
Vue.use(Container);
Vue.use(Header);
Vue.use(Main);
Vue.use(Footer);
Vue.use(Avatar);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
